import { Component, OnInit, OnDestroy  } from '@angular/core';
import { ArticuloService } from '../../../../core/consultaService/articulo.service';

import { NgxSpinnerService } from 'ngx-spinner';

import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';

import { ConsultasService } from '../../../../core/consultaService/consultas.service';
import { LineaPopUpComponent } from '../../../commonPopUp/lineaPopUp/lineaPopUp.component';

declare var jQuery:any;
declare var $:any;

const swal = require('sweetalert');

@Component({
  selector: 'app-linea',
  templateUrl: './linea.component.html',
  styleUrls: ['./linea.component.scss']
})
export class LineaComponent implements OnInit, OnDestroy {
  
  request: any = {};

  editField: string;

  lineaList: Array<any> = [];

  bsModalRef: BsModalRef;

  subData: any;

  constructor(private articuloService:ArticuloService, public cargador: NgxSpinnerService, private modalService: BsModalService,
              private consultasService:ConsultasService) { }

  ngOnInit() {

    this.cargarLineas();

    this.subData = this.consultasService.datos$.subscribe(resData =>{
      this.setData(resData);
    })
  }

  ngOnDestroy(){
    if(this.subData != undefined) {
      this.subData.unsubscribe();
    }
  } 

  cargarLineas(){

    this.request.scodestado = "";
    this.request.scodlinea = "";
    this.request.scodpais = "";

    this.cargador.show();

    this.articuloService.getLineas(this.request)
    .subscribe((data:any)=>{
      this.cargador.hide();
      this.lineaList = data.articulo;            
      
      if(data.error.codError != 0){
        swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
      }
    },
    error =>{
        this.cargador.hide();
        swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
    })
  }

  setData(resData){
    if(resData === 'guardar'){
        this.cargarLineas();
    }
  }

  openEditarLinea(linea){
    window.scroll(0,0);
    const initialState = {
      titulo: 'Editar',
      linea: linea
    };
    this.bsModalRef = this.modalService.show(LineaPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }

  openAgregarLinea(){
    const initialState = {
      titulo: 'Agregar'
    };
    this.bsModalRef = this.modalService.show(LineaPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }
}
