import { Component, OnInit, OnDestroy  } from '@angular/core';
import { ArticuloService } from '../../../../core/consultaService/articulo.service';

import { NgxSpinnerService } from 'ngx-spinner';

import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';

import { ConsultasService } from '../../../../core/consultaService/consultas.service';
import { ProductoBasePopUpComponent } from '../../../commonPopUp/productoBasePopUp/productoBasePopUp.component';

const swal = require('sweetalert');

@Component({
  selector: 'app-productoBase',
  templateUrl: './productoBase.component.html',
  styleUrls: ['./productoBase.component.scss']
})
export class ProductoBaseComponent implements OnInit {

  request: any = {};

  editField: string;

  prodBaseList: Array<any> = [];

  bsModalRef: BsModalRef;

  subData: any;
  
  constructor(private articuloService:ArticuloService, public cargador: NgxSpinnerService, private modalService: BsModalService,
    private consultasService:ConsultasService) { }

  ngOnInit() {

    this.cargarProductosBase();

    this.subData = this.consultasService.datos$.subscribe(resData =>{
      this.setData(resData);
    })
  }

  ngOnDestroy(){
    if(this.subData != undefined) {
      this.subData.unsubscribe();
    }
  } 

  cargarProductosBase(){
    this.request.scodagrupacion = "";
    this.request.scodcategoria = "";
    this.request.scodestado = "";

     this.cargador.show();
    
    this.articuloService.getProdBase(this.request)
      .subscribe((data:any)=>{
           this.cargador.hide();
          this.prodBaseList = data.articulo;            
          if(data.error.codError != 0){
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
          } 
      },
      error =>{
          this.cargador.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
      })
  }

  setData(resData){
    if(resData === 'guardar'){
        this.cargarProductosBase();
    }
  }

  openEditarProductoBase(prodBase){
    window.scroll(0,0);
    const initialState = {
      titulo: 'Editar',
      prodBase: prodBase
    };
    this.bsModalRef = this.modalService.show(ProductoBasePopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }

  openAgregarProductoBase(){
    const initialState = {
      titulo: 'Agregar'
    };
    this.bsModalRef = this.modalService.show(ProductoBasePopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }
}
