import { Component, OnInit, OnDestroy  } from '@angular/core';
import { ArticuloService } from '../../../../core/consultaService/articulo.service';

import { NgxSpinnerService } from 'ngx-spinner';

import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';

import { ConsultasService } from '../../../../core/consultaService/consultas.service';
import { AgrupacionPopUpComponent } from '../../../commonPopUp/agrupacionPopUp/agrupacionPopUp.component';

const swal = require('sweetalert');

@Component({
  selector: 'app-agrupacion',
  templateUrl: './agrupacion.component.html',
  styleUrls: ['./agrupacion.component.scss']
})
export class AgrupacionComponent implements OnInit, OnDestroy {

  request: any = {};

  editField: string;

  agrupaList: Array<any> = [];

  bsModalRef: BsModalRef;

  subData: any;

  constructor(private articuloService:ArticuloService, public cargador: NgxSpinnerService, private modalService: BsModalService,
    private consultasService:ConsultasService) { }

  ngOnInit() {

    this.cargarAgrupaciones();

    this.subData = this.consultasService.datos$.subscribe(resData =>{
      this.setData(resData);
    })
  }

  ngOnDestroy(){
    if(this.subData != undefined) {
      this.subData.unsubscribe();
    }
  } 

  cargarAgrupaciones(){

    this.request.scodagrupacion = "";
    this.request.scodcategoria = "";
    this.request.scodestado = "";

    this.cargador.show();
    
    this.articuloService.getAgrupacion(this.request)
      .subscribe((data:any)=>{
          this.cargador.hide();
          this.agrupaList = data.articulo;            
          if(data.error.codError != 0){
              swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
          } 
      },
      error =>{
          this.cargador.hide();
          swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
      })
  }

  setData(resData){

    if(resData === 'guardar'){
        this.cargarAgrupaciones();
    }
  }

  openEditarAgrupacion(agrupacion){
    window.scroll(0,0);
    const initialState = {
      titulo: 'Editar',
      agrupacion: agrupacion
    };
    this.bsModalRef = this.modalService.show(AgrupacionPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }

  openAgregarAgrupacion(){
    const initialState = {
      titulo: 'Agregar'
    };
    this.bsModalRef = this.modalService.show(AgrupacionPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }

}
