import { Component, OnInit, OnDestroy  } from '@angular/core';
import { ArticuloService } from '../../../../core/consultaService/articulo.service';

import { NgxSpinnerService } from 'ngx-spinner';

import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';

import { PaisPopUpComponent } from './../../../commonPopUp/paisPopUp/paisPopUp.component'
import { ConsultasService } from '../../../../core/consultaService/consultas.service';

const swal = require('sweetalert');

@Component({
  selector: 'app-pais',
  templateUrl: './pais.component.html',
  styleUrls: ['./pais.component.scss']
})
export class PaisComponent implements OnInit, OnDestroy  {

  request: any = {};

  editField: string;

  paisList: Array<any> = [];

  bsModalRef: BsModalRef;

  subData: any;

  constructor(private articuloService:ArticuloService, public cargador: NgxSpinnerService, private modalService: BsModalService,
              private consultasService:ConsultasService) { }

  ngOnInit() {

    this.cargarPaises();

    this.subData = this.consultasService.datos$.subscribe(resData =>{
      this.setData(resData);
    })
  }

  ngOnDestroy(){
    if(this.subData != undefined) {
      this.subData.unsubscribe();
    }
  } 

  cargarPaises(){
    this.request.scodestado = "";
    this.request.scodpais = "";

    this.cargador.show();
    
    this.articuloService.getPais(this.request)
        .subscribe((data:any)=>{
            this.cargador.hide();
            this.paisList = data.articulo;
  
            if(data.error.codError != 0){
                swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            } 
        },
        error =>{
            this.cargador.hide();
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        })
  }

  openEditarPais(pais){
    window.scroll(0,0);
    const initialState = {
      titulo: 'Editar',
      pais: pais
    };
    this.bsModalRef = this.modalService.show(PaisPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }

  openAgregarPais(){
    const initialState = {
      titulo: 'Agregar',
      origen: 'categoria'
    };
    this.bsModalRef = this.modalService.show(PaisPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }

  setData(resData){
    if(resData === 'guardar'){
        this.cargarPaises();
    }
  }
}
