import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { Ng2TableModule } from 'ng2-table/ng2-table';
import { NgxDatatableModule } from '@swimlane/ngx-datatable';
import { SharedModule } from '../../shared/shared.module';
import { NgxPaginationModule } from 'ngx-pagination';
import { CommonPopUpModule } from '../commonPopUp/commonPopUp.module';

import { BuscaArticuloComponent } from './buscaArticulo/buscaArticulo.component';
import { DefinicionArticuloComponent } from './definicionArticulo/definicionArticulo.component';
import { PackComponent } from './definicionArticulo/navDefinicionArticulo/pack/pack.component';
import { TotalesComponent } from './definicionArticulo/navDefinicionArticulo/pack/totales/totales.component';
import { JerarquiaComponent } from './jerarquia/jerarquia.component';
import { PaisComponent } from './jerarquia/pais/pais.component';
import { LineaComponent } from './jerarquia/linea/linea.component';
import { CategoriaComponent } from './jerarquia/categoria/categoria.component';
import { AgrupacionComponent } from './jerarquia/agrupacion/agrupacion.component';
import { ProductoBaseComponent } from './jerarquia/productoBase/productoBase.component';
import { BuscarArticuloPopUpComponent } from '../commonPopUp/buscarArticuloPopUp/buscarArticuloPopUp.component';
import { PaisPopUpComponent } from '../commonPopUp/paisPopUp/paisPopUp.component';
import { LineaPopUpComponent } from '../commonPopUp/lineaPopUp/lineaPopUp.component';
import { CategoriaPopUpComponent } from '../commonPopUp/categoriaPopUp/categoriaPopUp.component';
import { AgrupacionPopUpComponent } from '../commonPopUp/agrupacionPopUp/agrupacionPopUp.component';
import { ProductoBasePopUpComponent } from '../commonPopUp/productoBasePopUp/productoBasePopUp.component';

const routes: Routes = [
  { path: 'buscaArticulo', component: BuscaArticuloComponent },
  { path: 'detalleArticulo', component: DefinicionArticuloComponent },
  { path: 'jerarquia', component: JerarquiaComponent }
];

@NgModule({
  imports: [
    SharedModule,
    RouterModule.forChild(routes),
    Ng2TableModule,
    NgxDatatableModule,
    NgxPaginationModule,
    CommonPopUpModule
  ],
  
  declarations: [
    BuscaArticuloComponent,
    DefinicionArticuloComponent,
    PackComponent,
    TotalesComponent,
    JerarquiaComponent,
    PaisComponent,
    LineaComponent,
    CategoriaComponent,
    AgrupacionComponent,
    ProductoBaseComponent,
  ],

  entryComponents: [ BuscarArticuloPopUpComponent, PaisPopUpComponent, LineaPopUpComponent, CategoriaPopUpComponent, AgrupacionPopUpComponent, ProductoBasePopUpComponent],
  exports: [
      RouterModule
  ]
})

export class ArticuloModule { }
