import { Component, OnInit, OnDestroy  } from '@angular/core';
import { ArticuloService } from '../../../../core/consultaService/articulo.service';

import { NgxSpinnerService } from 'ngx-spinner';

import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';

import { ConsultasService } from '../../../../core/consultaService/consultas.service';
import { CategoriaPopUpComponent } from '../../../commonPopUp/categoriaPopUp/categoriaPopUp.component';

const swal = require('sweetalert');


@Component({
  selector: 'app-categoria',
  templateUrl: './categoria.component.html',
  styleUrls: ['./categoria.component.scss']
})
export class CategoriaComponent implements OnInit, OnDestroy {

  request: any = {};

  editField: string;

  catList: Array<any> = [];

  bsModalRef: BsModalRef;

  subData: any;

  constructor(private articuloService:ArticuloService, public cargador: NgxSpinnerService, private modalService: BsModalService,
    private consultasService:ConsultasService) { }

  ngOnInit() {

    this.cargarCategorias();

    this.subData = this.consultasService.datos$.subscribe(resData =>{
      this.setData(resData);
    })
  }

  ngOnDestroy(){
    if(this.subData != undefined) {
      this.subData.unsubscribe();
    }
  } 

  cargarCategorias(){

    this.request.scodcategoria = "";
    this.request.scodestado = "";
    this.request.scodlinea = "";

    this.cargador.show();
    
    this.articuloService.getCategorias(this.request)
        .subscribe((data:any)=>{
            this.cargador.hide();
            this.catList = data.articulo;            
            if(data.error.codError != 0){
                swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
            } 
        },
        error =>{
            this.cargador.hide();
            swal('Atención', 'Ha ocurrido un error inesperado!', 'error');
        })

  }

  setData(resData){
    if(resData === 'guardar'){
        this.cargarCategorias();
    }
  }

  openEditarCategoria(categoria){
    window.scroll(0,0);
    const initialState = {
      titulo: 'Editar',
      categoria: categoria
    };
    this.bsModalRef = this.modalService.show(CategoriaPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }

  openAgregarCategoria(){
    const initialState = {
      titulo: 'Agregar'
    };
    this.bsModalRef = this.modalService.show(CategoriaPopUpComponent, {initialState, backdrop: 'static', keyboard: false});
  }


}
